define('timesheetUtils',['moment'], function(moment) {

window.TimesheetUtils = {};
window.moment = moment; // FIXME: convert PivotTable to module

TimesheetUtils.getLastMondayDate = function() {
    return TimesheetUtils.getPrevMonday(new Date());
};

TimesheetUtils.getPrevMonday = function(date) {
    return TimesheetUtils.getNearestDayOfTheWeek(date, 1, true);
};

TimesheetUtils.getNextSunday = function(date) {
    return TimesheetUtils.getNearestDayOfTheWeek(date, 0, false);
};

TimesheetUtils.getNearestDayOfTheWeek = function(date, dayOfTheWeek, isBefore) {
    while (date.getDay() != dayOfTheWeek) {
        date = TimesheetUtils.addDays(date, isBefore ? -1 : 1);
    }
    return TimesheetUtils.truncateTime(date);
};

TimesheetUtils.getFirstDayOfTheMonth = function(date) {
    if (date == null) {
        date = new Date();
    }
    return TimesheetUtils.truncateTime(moment(date).startOf('month').toDate());
};

TimesheetUtils.getLastDayOfTheMonth = function(date) {
    if (date == null) {
        date = new Date();
    }
    return TimesheetUtils.truncateTime(moment(date).endOf('month').toDate());
};

TimesheetUtils.truncateTime = function(date) {
    return new Date(date.getFullYear(), date.getMonth(), date.getDate());
};

TimesheetUtils.truncateMinutes = function(date) {
    return new Date(date.getFullYear(), date.getMonth(), date.getDate(), date.getHours());
};

// show work on the day it happened
// date is in server timezone, so convert it to worked user time zone
TimesheetUtils.convertDate = function(dateString, workTimeZone) {
    if (workTimeZone) {
        var workDate = moment.tz(dateString, workTimeZone).format().replace(/[+-]\d\d:?\d\d$/, '');
        var converted = moment(workDate).toDate();
        return converted;
    } else {
        return moment(dateString).toDate();        
    }
};

// not used
TimesheetUtils.getTimezoneOffsetBak = function(dateString) {
    var date = moment(dateString).toDate();
    var time = date.getTime();
    if (TimesheetUtils.timezoneOffset == undefined) {
        TimesheetUtils.timezoneOffset = {};
    } else if (TimesheetUtils.timezoneOffset[time] !== undefined) {
        return TimesheetUtils.timezoneOffset[time];
    }
    // http://stackoverflow.com/a/5114625/922115
    function pad(number, length){
        var str = "" + number;
        while (str.length < length) {
            str = '0' + str;
        }
        return str
    }

    var offset = date.getTimezoneOffset();
    TimesheetUtils.timezoneOffset[time] = ((offset < 0 ? '+' : '-') + // Note the reversed sign!
              pad(parseInt(Math.abs(offset / 60)), 2) +
              pad(Math.abs(offset % 60), 2));
    return TimesheetUtils.timezoneOffset[time];
};

TimesheetUtils.addTime = function(date, diff, measure) {
    return moment(date).add(diff, measure).toDate();
};

TimesheetUtils.addDays = function(date, add) {
    return TimesheetUtils.addTime(date, add, 'days');
};

TimesheetUtils.addWeeks = function(date, add) {
    return TimesheetUtils.addTime(date, add, 'weeks');
};

TimesheetUtils.addMonths = function(date, add) {
    return TimesheetUtils.addTime(date, add, 'months');
};

TimesheetUtils.addHours = function(date, add) {
    return TimesheetUtils.addTime(date, add, 'hours');
};

TimesheetUtils.sameDay = function(date1, date2) {
    return date1.getDate() == date2.getDate()
        && date1.getFullYear() == date2.getFullYear()
        && date1.getMonth() == date2.getMonth();
};

TimesheetUtils.isDate = function(value) {
    return typeof value === 'string' && moment(new Date(value)).isValid();
};

TimesheetUtils.colspan = function(table) {
    if (table != null) {
        var rows = table.rows;
        var keys = Object.keys(rows);
        if (keys.length > 0) {
            var rowKey = rows[keys[0]].rowKey;
            if (rowKey.issue || rowKey.field) {
                return 5;
            }
        }
    }
    return 1;
};

TimesheetUtils.getPrettyHours = function(seconds) {
    return seconds > 0 ? seconds/3600 + 'h' : '';
};

TimesheetUtils.getWeekNumber = function(date) {
    return moment(date).isoWeek();
};

TimesheetUtils.getMonthKey = function(date) { // e.g.: 2015-12
    return moment(date).format('YYYY-MM');
};

TimesheetUtils.getNormalizedWeekNumber = function(date) {
    var week = TimesheetUtils.getWeekNumber(date);
    return TimesheetUtils.normalizeNumber(week);
};

TimesheetUtils.normalizeNumber = function(number){
    return (number < 10 ? '0' : '') + number;
};

TimesheetUtils.formatDate = function(date) {
    return moment(date).toJSON().replace("Z", "+0000");
};

TimesheetUtils.getHoursForDay = function(options) {
    var result = [];
    var startDate = options.startDate;
    var endDate = options.endDate;
    if (startDate.getTime() <= endDate.getTime()) {
        var now = new Date();
        while (startDate.getHours() != endDate.getHours()) {
            var isToday = startDate.getHours() == now.getHours();
            result.push({
                'date': startDate,
                'isToday': isToday
            });
            startDate = TimesheetUtils.addHours(startDate, 1);
        }
    }
    return result;
};

TimesheetUtils.getDatesForWeek = function(options) {
    var result = [];
    var startDate = options.startDate;
    var endDate = options.endDate;
    if (startDate.getTime() <= endDate.getTime()) {
        endDate = TimesheetUtils.addDays(endDate, 1); // endDate should be in range
        while (!TimesheetUtils.sameDay(startDate, endDate)) {
            var isToday = TimesheetUtils.sameDay(startDate, TimesheetUtils.truncateTime(new Date()));
            result.push({
                'date': startDate,
                'isToday': isToday
            });
            startDate = TimesheetUtils.addDays(startDate, 1);
        }
    }
    return result;
};

TimesheetUtils.getWeeksForMonth = function(options) {
    var result = [];
    var startMonday = TimesheetUtils.getPrevMonday(options.startDate);
    var endSunday = TimesheetUtils.getNextSunday(options.endDate);
    var currentWeekNumber = TimesheetUtils.getWeekNumber(new Date());
    var date = startMonday;
    while (date.getTime() <= endSunday.getTime()) {
        var wNum = TimesheetUtils.getWeekNumber(date);
        result.push({
            'week': 'Week ' + wNum,
            'date': date,
            'isToday': wNum == currentWeekNumber
        });
        date = TimesheetUtils.addDays(date, 7);
    }
    return result;
};

TimesheetUtils.getMonthForYear = function(options) {
    var result = [];
    var firstDay = TimesheetUtils.getFirstDayOfTheMonth(options.startDate);
    var lastDay = TimesheetUtils.getLastDayOfTheMonth(options.endDate);
    var currentMonth = TimesheetUtils.getMonthKey(new Date());
    var date = firstDay;
    while (date.getTime() <= lastDay.getTime()) {
        var monthNum = TimesheetUtils.getMonthKey(date);
        result.push({
            'month': moment.months()[monthNum],
            'date': date,
            'isToday': monthNum == currentMonth
        });
        date = TimesheetUtils.addMonths(date, 1);
    }
    return result;
};

TimesheetUtils.changeDate = function(date, offset, nullable, isMonthView) {
    if (nullable && date == null) {
        return null;
    } else {
        if (date == null) {
            date = isMonthView ? TimesheetUtils.getFirstDayOfTheMonth() : TimesheetUtils.getLastMondayDate();
        }
        date = isMonthView ? TimesheetUtils.addMonths(date, offset) : TimesheetUtils.addDays(date, offset);
        return TimesheetUtils.formatDateForSearch(date);
    }
};

TimesheetUtils.formatDateForSearch = function(date) {
    return date.getFullYear() + "-" + TimesheetUtils.normalizeNumber(date.getMonth() + 1) + "-" + TimesheetUtils.normalizeNumber(date.getDate());
};

TimesheetUtils.filterFields = function(fields) {
    var result = [];
    for (var i = 0; i < fields.length; i++) {
        var field = fields[i];
        if (field.schema != null && (([
                'number', 'string', 'project', 'issuetype', 'securitylevel',
                'resolution', 'user', 'priority', 'status', 'datetime', 'date',
                'option'
            ].indexOf(field.schema.type) >= 0 && ['summary', 'description'].indexOf(field.id) == -1)
            || (field.schema.type == 'array'))) {
            result.push(field);
        }
    }
    return result;
};

TimesheetUtils.sortByProperty = function(array, propertyName, desc) {
    var sign = !desc || -1;
    return array.sort(function(a, b){
        if (typeof a[propertyName] == 'string') {
            return sign * a[propertyName].localeCompare(b[propertyName]);
        } else { // Number
            return sign * (a[propertyName] - b[propertyName]);
        }
    });
};

TimesheetUtils.getJson = function(data) {
    if (angular.isString(data)) {
        data = data != "" ? JSON.parse(data) : {};
    }
    return data;
};

TimesheetUtils.makeUISelectList = function(stringArray, addEmpty) {
    var makeSelectItemObject = function(name) {
        return {id: name, label: name}
    };

    var result = [];
    if (addEmpty) {
        result.push(makeSelectItemObject(''))
    }
    for (var i = 0; i < stringArray.length; i++) {
        result.push(makeSelectItemObject(stringArray[i]))
    }
    return result;
};

TimesheetUtils.setDynamicParams = function(params) {
    AP.require('events', function(events) {
        events.on('getDynamicParams', function() {
            events.emit('loadDyamicParams', params);
            events.offAll('getDynamicParams');
        });
    });
};

TimesheetUtils.getDynamicParams = function(callback) {
    AP.require('events', function(events){
        events.on('loadDyamicParams', function(params){
            events.offAll('setFeedbackToDelete');
            callback(params);
        });
        events.emit('getDynamicParams');
    });
};

TimesheetUtils.convertHoursToFormat24 = function (hour, isPM) {
    if (isPM) {
        return (hour + 12) % 24;
    }
    return hour;
};

TimesheetUtils.convertHoursToFormat12 = function (hour) {
    if (hour > 12 || hour == 0) {
        return {hour: Math.abs(hour - 12), pm: true};
    }
    return {hour: hour, pm: false};
};

TimesheetUtils.getCurrentTimeMs = function() {
    var curDate = new Date();
    return (curDate.getHours()*60*60 + curDate.getMinutes()*60) * 1000;
};

// see also timespent directive in modules/directives.js
TimesheetUtils.getTimeSpentMs = function(timeSpent, config) {
    if (!timeSpent) {
        return 0;
    }
    var getMs = function(value) {
        var unit = config.defaultUnit;
        var quantity = 0;
        if (isNaN(value)) {
            var parsed = value.match(/(\d*\.?\d+)([a-zA-Z]*)/);
            if (parsed) {
                quantity = parseFloat(parsed[1]);
                unit = parsed[2];
            }
        } else {
            quantity = parseFloat(value);
        }
        var sec = 1000;
        var min = 60 * sec;
        var hour = 60 * min;
        var day = config.workingHoursPerDay * hour;
        var week = config.workingDaysPerWeek * day;
        switch (unit) {
            case 'w':
            case 'week':
                return quantity * week;
            case 'd':
            case 'day':
                return quantity * day;
            case 'h':
            case 'hour':
                return quantity * hour;
            case 'm':
            case 'minute':
                return quantity * min;
            case 's':
                return quantity * sec;
        }
    };
    var blocks = timeSpent.split(" ");
    var result = 0;
    for (var i = 0; i < blocks.length; i++) {
        result += getMs(blocks[i]);
    }
    return result;
};

TimesheetUtils.getSearchQueryBuilder = function(options) {
    
    var getFilterOrProjectValue = function(options, startString) {
        var result = null;
        if (options.filterOrProjectId) {
            var items = angular.isArray(options.filterOrProjectId)
                ? options.filterOrProjectId
                : options.filterOrProjectId.split(',');
            result = [];
            for (var i = 0; i < items.length; i++) {
                if (items[i].indexOf(startString) == 0) {
                    result.push(items[i].split(startString)[1]);
                }
            }
        }
        return result;
    };
    var getOrQueryBuilder = function(paramName, items) {
        var qb = new TimesheetUtils.queryBuilder(" or ");
        for (var i = 0; i < items.length; i++) {
            qb.addQuery(paramName, '"' + items[i] + '"');
        }
        return qb;
    };
    var filterIds = getFilterOrProjectValue(options, 'filter_');
    var projectKeys = getFilterOrProjectValue(options, 'project_');
    if (!filterIds && !projectKeys && options.projectKey) {
        projectKeys = [options.projectKey];
    }

    // performance optimization hack, see pivottableService.checkIfMatches()
    options.projectKeys = projectKeys;
    options.filterIds = filterIds;

    var jqlQueryBuilder = new TimesheetUtils.queryBuilder(" and ");
    if (filterIds && filterIds.length > 0) {
        // https://answers.atlassian.com/questions/22264/can-filters-be-used-with-the-rest-api
        if (filterIds.length == 1) {
            jqlQueryBuilder.addQuery('filter', filterIds[0]);
        } else {
            jqlQueryBuilder.addQueryBuilder(getOrQueryBuilder('filter', filterIds));
        }
    }
    if (projectKeys && projectKeys.length > 0) {
        // https://answers.atlassian.com/questions/155180/filter-issue-by-project-key
        if (projectKeys.length == 1) {
            jqlQueryBuilder.addQuery('project', '"' + projectKeys[0] + '"');
        } else {
            jqlQueryBuilder.addQueryBuilder(getOrQueryBuilder('project', projectKeys));
        }
    }
    return jqlQueryBuilder;
};

TimesheetUtils.queryBuilder = function(delimiter) {
    var items = [];
    delimiter = delimiter || "&";

    this.addQuery = function(param, value, delimiter) {
        delimiter = delimiter || "=";
        if (value != null && value != "") {
            items.push(param + delimiter + value);
        }
        return this;
    };

    this.addQueryWithFunc = function(param, value, delimiter, queryFunc) {
        this.addQuery(param, queryFunc + "(" + value + ")", delimiter);
    };

    this.addQueryBuilder = function(queryBuilder) {
        items.push("(" + queryBuilder.build() + ")");
    };

    this.build = function() {
        return this.isEmpty()
            ? ""
            : items.join(delimiter);
    };

    this.isEmpty = function() {
        return items == null || items.length == 0;
    };
};

TimesheetUtils.closeDialogFunctions = {
    submit: function() {
        AP.Dialog.close('submit');
    },
    cancel: function() {
        AP.Dialog.close('cancel');
    }
};

TimesheetUtils.normalizeObject = function(obj, removeEmpty) {
    var keys = Object.keys(obj);
    for (var i = 0; i < keys.length; i++) {
        if (obj[keys[i]] == null || removeEmpty && (obj[keys[i]] == "" || obj[keys[i]] == [])) {
            delete obj[keys[i]];
        }
    }
    return obj;
};

return TimesheetUtils;

});
