var PivotKey = {};

PivotKey.Generic = function (keyName, keyValue, filterName) {
    this.keyName = keyName;
    this.keyValue = keyValue;
    this.filterName = filterName;
};

PivotKey.Issue = function(issue, worklog, configOptions) {
    this.issue = issue;
    this.keyName = "issue";
    this.keyValue = this.issue.key;
    this.worklog = worklog;
};

PivotKey.User = function(worklog) {
    this.user = worklog.author;
    this.keyName = "user";
    this.keyValue = worklog.author ? worklog.author.name : 'anonymous';
};

PivotKey.getStatus = function(worklog, issue) {
    var workDate = TimesheetUtils.convertDate(worklog.started);
    var status = issue.fields.status.name;
    var statusDate = new Date();
    var changelog = issue.changelog ? issue.changelog : {};
    var histories = changelog.histories ? changelog.histories : [];
    for (var i = 0; i < histories.length; i++) {
        var history = histories[i];
        var items = history.items || [];
        var created = TimesheetUtils.convertDate(history.created);
        for (var j = 0; j < items.length; j++) {
            var item = items[j];
            if (item.fieldtype == 'jira' && item.field == 'status' && workDate < created && statusDate > created) {
                status = item.fromString;
                statusDate = created;
            }
        }
    }
    return status;
};

PivotKey.getStatuses = function(issue) {
    var changelog = issue.changelog ? issue.changelog : {};
    var histories = changelog.histories ? changelog.histories : [];
    var statuses = [];
    histories.map(function(history) {
        var created = TimesheetUtils.convertDate(history.created);
        var items = history.items || [];
        items.map(function(item) {
            if (item.fieldtype == 'jira' && item.field == 'status') {
                var status = {
                    name: item.fromString,
                    date: created.getTime()
                };
                statuses.push(status);
            }
        });
    });
    statuses.push({ // latest
        name: issue.fields.status.name,
        date: Date.now()
    });
    TimesheetUtils.sortByProperty(statuses, 'date');
    return statuses;
};

PivotKey.getStatusKeys = function(worklog, issue) {
    var statuses = PivotKey.getStatuses(issue);
    var timespent = worklog.timeSpentSeconds;
    var workDate = TimesheetUtils.convertDate(worklog.started).getTime();
    var result = [];
    for (var i = 0; i < statuses.length - 1 && timespent > 0 && workDate < statuses[i].date; i++) {
        var status = statuses[i];
        var elapsed = (status.date - workDate)  / 1000;
        var statusKey = {
            key: new PivotKey.Status(status.name),
            timespent: Math.min(timespent, elapsed)
        };
        result.push(statusKey);
        // remaining work
        workDate = status.date;
        timespent -= statusKey.timespent;
    }
    if (timespent > 0) { // latest
        var statusKey = {
            key: new PivotKey.Status(statuses[statuses.length - 1].name),
            timespent: timespent
        };
        result.push(statusKey);
    }
    return result;
};

PivotKey.Status = function(worklog, issue) {
    this.keyName = "status";
    if (typeof worklog == 'string') {
        this.keyValue = worklog; // status
    } else { // TODO: remove backup case
        this.keyValue = PivotKey.getStatus(worklog, issue);
    }
};

PivotKey.HourOfTheDay = function(date) {
	this.keyName = "dayOfTheWeek";
    this.date = TimesheetUtils.truncateMinutes(date);
    this.keyValue = this.date.getTime();
};

PivotKey.DayOfTheWeek = function(date) {
	this.keyName = "dayOfTheWeek";
    this.date = TimesheetUtils.truncateTime(date);
    this.keyValue = this.date.getTime();
};

PivotKey.WeekOfTheYear = function(date) {
    this.keyName = "weekOfTheYear";
    this.startDate = TimesheetUtils.getPrevMonday(date);
    this.endDate = TimesheetUtils.addDays(date, 7);
    this.weekNumber = TimesheetUtils.getNormalizedWeekNumber(date); //normalize for correct ordering
    this.keyValue = this.startDate.getTime();
};

PivotKey.MonthOfTheYear = function(date) {
    this.keyName = "monthOfTheYear";
    this.startDate = TimesheetUtils.getFirstDayOfTheMonth(date);
    this.endDate = TimesheetUtils.getLastDayOfTheMonth(date);
    this.monthKey = TimesheetUtils.getMonthKey(date);
    this.keyValue = this.startDate.getTime();
};

PivotKey.makeIssueFieldKey = function(fieldName) {
    var NULL_VALUE = "&nbsp;";

    var getNameOrKey = function(field) {
        if (typeof field === 'object' && field.hasOwnProperty('name')) {
            return field.name;
        } else if (typeof field === 'object' && field.hasOwnProperty('key')) {
            return field.key;
        } else if (typeof field === 'object' && field.hasOwnProperty('value')) {
            return field.value;
        } else {
            return field;
        }
    };

    var getValue = function(field) {
        if (TimesheetUtils.isDate(field)) {
            return TimesheetUtils.truncateTime(moment(field).toDate());
        } else if (Array.isArray(field)) {
            var value = '';
            field.forEach(function(v) {
                if (value.length > 0) {
                    value += ', ';
                }
                value += getNameOrKey(v);
            });
            return value;
        } else {
            return getNameOrKey(field);
        }
    };

    return function(issue, worklog, configOptions) {
        this._issue = issue;
        var field = this._issue.fields[fieldName] || (fieldName == 'parent' ? {key : issue.key} : NULL_VALUE);
        this.field = fieldName != 'project' ? field : NULL_VALUE;
        this.project = this._issue.fields.project;
        this.keyName = fieldName;
        this.keyValue = this.project.key + ":" + getValue(this.field);
    }
};

PivotKey.groupByWorkedUserKey = function(issue, worklog, configOptions) {
    var NULL_VALUE = "&nbsp;";
    this._issue = issue;
    this._worklog = worklog;
    this.field = this._worklog.author || NULL_VALUE;
    this.keyName = "workeduser";
    this.keyValue = this.field.name || NULL_VALUE;
};

define("app/pivottable/PivotKey", function(){});

