
require.config({
    baseUrl: '/js',
    paths: {
        'moment': 'bower_components/moment/moment'
    }
});
require([
    "lib/zef-angularjs-i18n/localization",
    "lib/jquery.base64",
    "bower_components/sprintf/src/sprintf",
    "bower_components/moment-timezone/builds/moment-timezone-with-data-2010-2020",
    "timesheetUtils",
    "app/modules/configuration",
    "app/modules/directives",
    "app/template/GadgetSummary",
    "app/template/TimesheetMenu",
    "app/controllers/gadgetController",
    "app/controllers/bitbucketController",
    "app/controllers/timesheetController",
    "app/controllers/configurationController",
    "app/controllers/worklogController",
    "app/services/pivottableService",
    "app/directives/DrawHelper",
    "app/directives/timesheetDirectives",
    "app/filters/timesheetFilters",
    "app/pivottable/PivotKey",
    "app/pivottable/PivotRow",
    "app/pivottable/PivotColumn",
    "app/pivottable/PivotEntry",
    "app/pivottable/TimesheetStrategy",
    "app/pivottable/PivotStrategy",
    "app/pivottable/PivotTable",
    "app/pivottable/ExcelView"
    ], function () {
        //https://developer.atlassian.com/static/connect/docs/1.1.33/guides/connect-cookbook.html#all.js
        var getUrlParam = function (param, defaultValue) {
            var matches = (new RegExp('\\W' + param + '=([^&]+)')).exec(window.location.search);
            var codedParam = matches ? matches[1] : (defaultValue || '');
            return decodeURIComponent(codedParam);
        };

        var baseUrl = getUrlParam('xdm_e') + getUrlParam('cp');

        if (!baseUrl) return; // tests
        if (window.AP) { // node app.js
            $(function() {
                angular.bootstrap(document, ['timesheetApp']);
            });
            return;
        }

        var b = function() { // buildNumber
            var scripts = document.getElementsByTagName('script');
            for (var i = 0; i < scripts.length; i += 1) {
                var script = scripts[i];
                var src = script.getAttribute('src');
                var m;
                if (src && (m = src.match(/\/app.js\?_=(\d+)/))) {
                    return m[1];
                }
            }
            return 1;
        }();

        var isConfiguration = getUrlParam('configuration');
        var dashboard = getUrlParam('dashboard');
        var isGadget = !!dashboard;
        var dialog = getUrlParam('dialog');
        if (isGadget || dialog) {
            $('body').css('background', '#fff');
        }
        if (isGadget) {
            $('body').addClass('is-gadget');
        } else {
            $('body').addClass('is-not-gadget');
        }

        var v = getUrlParam('v', 1); // add-on version
        if (v == '1') { // TODO: remove
            mailerUrl = undefined;
        }

        var params = {
            isGadget: isGadget,
            dashboardId: dashboard,
            dashboardItemId: getUrlParam('dashboardItem'),
            license: getUrlParam('lic'),
            hostBaseUrl: baseUrl,
        };

        if (isConfiguration) {
            window.mainController = 'ConfigurationController';
            window.mainTemplate = '/templates/mainShort.html?_=' + b;
            params.templates = {
                menu: '/templates/configMenu.html?_=' + b,
                configure: '/templates/configure.html?_=' + b
            };
        } else if (dialog) {
            if (['deleteworklog', 'updateworklog', 'createworklog', 'addworklog'].indexOf(dialog) >= 0) {
                window.mainController = 'WorklogController';
                window.mainTemplate = '/templates/dialog.html?_=' + b;
                params.templates = {};
            }
            switch (dialog) {
                case 'deleteworklog':
                    params.dialogType = 'delete';
                    params.templates.dialog = '/templates/dialogs/deleteWorklog.html?_=' + b;
                    break;
                case 'updateworklog':
                    params.dialogType = 'update';
                    params.templates.dialog = '/templates/dialogs/updateWorklog.html?_=' + b;
                    break;
                case 'createworklog':
                    params.dialogType = 'create';
                    params.templates.dialog = '/templates/dialogs/updateWorklog.html?_=' + b;
                    break;
                case 'addworklog':
                    params.dialogType = 'add';
                    params.templates.dialog = '/templates/dialogs/updateWorklog.html?_=' + b;
                    break;
                case 'bitbucket':
                    window.mainController = 'BitbucketController';
                    window.mainTemplate = '/templates/dialogs/bitbucket.html?_=' + b;
                    break;
            }
        } else {
            window.mainController = 'TimesheetController';
            window.mainTemplate = '/templates/main.html?_=' + b;
            params.templates = {
                header: '/templates/header.html?_=' + b,
                menu: '/templates/menu.html?_=' + b,
                pivottable: '/templates/pivottable.html?_=' + b,
                piechart: '/templates/pieChart.html?_=' + b,
                timesheet: '/templates/timesheet.html?_=' + b,
                editWorklog: '/templates/popups/editWorklog.html?_=' + b,
                gadgetConfig: '/templates/gadgetConfig.html?_=' + b
            }
        }
        window.i18nDefault = '/i18n/default.json?_=' + b;

        $.getScript(baseUrl + '/atlassian-connect/all.js', function() {
            $(function() {
                $('#content').html("<context items='" + JSON.stringify(params) + "' />");
                angular.bootstrap(document, ['timesheetApp']);
            });
        });
    }
);

define("app", function(){});

