function TimesheetSelectOptions(optionClass, selected, sortableProperty) {
    this.options = [];
    this.optionClass = optionClass;
    this.sortableProperty = sortableProperty != null ? sortableProperty : "label";
    this.selected = selected || "";
}

TimesheetSelectOptions.prototype.addOption = function(label, key, addToHead) {
    var newOption = new this.optionClass(label, key);
    if (addToHead == true) {
        this.options.unshift(newOption);
    }
    else {
        this.options.push(newOption);
    }
    return this;
};

TimesheetSelectOptions.prototype.findOption = function(value) {
    for (var i = 0; i < this.options.length; i++) {
        if (this.options[i].isOption(value)) {
            return this.options[i];
        }
    }
    return null;
};

TimesheetSelectOptions.prototype.sort = function(desc) {
    TimesheetUtils.sortByProperty(this.options, this.sortableProperty, desc);
};

function TimesheetGeneralOption(label, id) {
    this.label = label;
    this.id = id;
}

TimesheetGeneralOption.prototype.isOption = function(id) {
    return this.id == id;
};

define("app/template/TimesheetMenu", function(){});

