define('app/controllers/bitbucketController',['app/timesheetApp'], function(app) {

app.controller('BitbucketController', ['$scope', '$http', '$window', 'loggedInUser',
    function ($scope, $http, $window, loggedInUser) {

        var AP = window.RA || window.AP;

        init();

        function init() {
            AP.resize();

            $scope.replyTo = loggedInUser.fullName + ' <' + loggedInUser.emailAddress + '>';

            var closeDialogFunctions = {
                submit: function() {
                    $scope.$apply(function() {
                        $scope.loading = true;
                    });
                    $http.post($window.mailerUrl, {replyTo: $scope.replyTo, title: $scope.title, content: $scope.content}).then(function(created) {
                        AP.Dialog.close('submit');
                    })
                },
                cancel: function() {
                    AP.Dialog.close('cancel');
                }
            };

            TimesheetUtils.getDynamicParams(function(params) {
                $scope.title = params.errorMessage;
                if (params.stackTrace) {
                    var content = ['', '', '---- Error Stack Trace ----'];
                    content.push.apply(content, params.stackTrace);
                    $scope.content = content.join('\n');
                }
                $scope.$apply();
            });

            AP.require('dialog', function (dialog) {
                dialog.getButton('submit').bind(closeDialogFunctions.submit);
                dialog.getButton('cancel').bind(closeDialogFunctions.cancel);
            });            
        }

    }
]);

});
