function WorkedTimePivotStrategy(rowKeyClass, columnKeyClass, options) {
    this.rowKeyClass = rowKeyClass;
    this.columnKeyClass = columnKeyClass;
    this.options = WorkedTimePivotStrategy.getStrategyOptions(options);
    this.configOptions = options.configOptions;
}

WorkedTimePivotStrategy.getStrategyOptions = function(options) {
    var result = {
        startDate: options.startDate,
        endDate: options.endDate
    };
    if (options.worklogAuthors) {
        result.worklogAuthors = options.worklogAuthors;
    }
    result.pivotByStatus = options.pivotTableType == "IssueWorkedTimeByStatus"; // spread timespent over statuses
    return result;
};

WorkedTimePivotStrategy.prototype.getEntries = function(issue) {
    var entries = [];
    var issueWorklog = issue.worklog || issue.fields.worklog;
    for (var i = 0; i < issueWorklog.worklogs.length; i++) {
        var worklog = issueWorklog.worklogs[i];
        var rowKey = new this.rowKeyClass(issue, worklog, this.configOptions);
        var inRange = true;
        var workDate = TimesheetUtils.convertDate(worklog.started, worklog.author ? worklog.author.timeZone : null);
        if (this.options.startDate) {
            inRange = workDate >= this.options.startDate;
        }
        if (this.options.endDate) {
            var endDateIncluded = TimesheetUtils.addDays(this.options.endDate, 1); // endDate should be in range
            inRange = inRange && (workDate < endDateIncluded);
        }
        var inGroups = (!this.options.worklogAuthors ||  // filtering by author
            (worklog.author && // FIXME: add worklogAuthorNames for ACJIRA-300
                this.options.worklogAuthors.indexOf(worklog.author.key ? worklog.author.key : worklog.author.name.toLowerCase()) >= 0));
        if (inRange && inGroups) {
            var entry;
            if (this.options.pivotByStatus) {
                var columnKeys = PivotKey.getStatusKeys(worklog, issue);
                entry = new PivotEntry(rowKey, columnKeys[0].key, worklog.timeSpentSeconds);
                entry.values[columnKeys[0].key.keyValue] =  columnKeys[0].timespent; // hack totals
                entry.worklog = worklog;
                for (var j = 1; j < columnKeys.length; j++) {
                    entry.addColumn(columnKeys[j].key, columnKeys[j].timespent);
                }
            } else {
                var columnKey = new this.columnKeyClass(worklog, issue);
                entry = new WorkedTimePivotEntry(rowKey, columnKey, worklog);
            }
            entries.push(entry);
        }
    }
    return entries;
};

function TimeTrackingStrategy(rowKeyClass, options) {
    this.rowKeyClass = rowKeyClass;
    this.options = {};
    this.configOptions = options.configOptions;
}
TimeTrackingStrategy.prototype.getEntries = function(issue) {
    var rowKey = new this.rowKeyClass(issue, this.configOptions);
    var plannedColumnKey = new PivotKey.Generic("PlannedVsActual", "1timeoriginalestimate");
    var remainColumnKey = new PivotKey.Generic("PlannedVsActual", "2esttimeremaining");
    var actualColumnKey = new PivotKey.Generic("PlannedVsActual", "3timespent");
    var diffColumnKey = new PivotKey.Generic("PlannedVsActual", "4diff");
    var progressColumnKey = new PivotKey.Generic("PlannedVsActual", "5progress", 'timeTracking');
    var original = issue.fields.timeoriginalestimate || 0;
    var entry = new PivotEntry(rowKey, plannedColumnKey, original);
    var actual = issue.fields.timespent || 0;
    var remaining = issue.fields.timeestimate || 0;
    entry.addColumn(remainColumnKey, remaining);
    entry.addColumn(actualColumnKey, actual);
    entry.addColumn(diffColumnKey, original - actual - remaining);
    entry.addColumn(progressColumnKey, {estimate: remaining, timespent: actual});
    return [entry];
};
define("app/pivottable/PivotStrategy", function(){});

