define('app/controllers/configurationController',['app/timesheetApp'], function(app) {

app.controller('ConfigurationController', ['$scope', '$route', '$routeParams', '$location',
       '$q', '$sce', '$timeout', '$window', 'configurationService', 'loggedInUser',
    function ($scope, $route, $routeParams, $location,
        $q, $sce, $timeout, $window, configurationService, loggedInUser) {

        var AP = window.RA || window.AP;

        init();

        function showSaveSuccess(varName) {
            $scope[varName] = true;
            $timeout(function(){$scope[varName] = false;}, 2000);
        }

        function init() {
            AP.resize();

            $scope.contentTemplate = function() {
                return $scope.templates['configure']; // furl
            };

            $scope.saveAuditorsGroups = function() {
                configurationService.saveConfiguration($scope.auditorsGroups).then(function(config){
                    $scope.auditorsGroups = config;
                    showSaveSuccess('auditorsGroupsSaveSuccess');
                });
            };

            $scope.saveCompositionIssueLink = function() {
                configurationService.saveConfiguration($scope.compositionIssueLink).then(function(config){
                    $scope.compositionIssueLink = config;
                    showSaveSuccess('compositionIssueLinkSaveSuccess');
                })
            };

            $scope.saveParentIssueField = function() {
                configurationService.saveConfiguration($scope.parentIssueField).then(function(config){
                    $scope.parentIssueField = config;
                    showSaveSuccess('parentIssueFieldSaveSuccess');
                })
            };
            
            $scope.saveOnErrorIgnore = function() {
                configurationService.saveConfiguration($scope.onErrorIgnore).then(function(config){
                    $scope.onErrorIgnore = config;
                    showSaveSuccess('onErrorIgnoreSaveSuccess');
                })
            };

            var $config = configurationService.getConfiguration();
            var $allGroups = configurationService.getGroups();
            var $availableLinks = configurationService.getAllCompositionIssueLinks();
            var $parentIssueFields = configurationService.getAllParentIssueFields();
            
            $q.all([$config, $allGroups, $availableLinks, $parentIssueFields]).then(function (args) {
                AP.resize();

                var config = args[0]
                var allGroups = args[1];
                var availableLinks = args[2];
                var parentIssueFields = args[3];

                var correctEmptyValue = function(option) {
                    if (option.val == null) {
                        option.val = '';
                    }
                };

                $scope.auditorsGroups = config.auditorsGroups;
                $scope.compositionIssueLink = config.compositionIssueLink;
                correctEmptyValue($scope.compositionIssueLink);
                $scope.parentIssueField = config.parentIssueField;
                correctEmptyValue($scope.parentIssueField);
                $scope.onErrorIgnore = config.onErrorIgnore;

                $scope.allGroups = TimesheetUtils.makeUISelectList(allGroups, false);
                $scope.allCompositionIssueLinks = TimesheetUtils.makeUISelectList(availableLinks, true);
                if (parentIssueFields != null && parentIssueFields.length > 0) {
                    $scope.allParentIssueFields = [{id: '', label: ''}];
                    for (var i = 0; i < parentIssueFields.length; i++) {
                        $scope.allParentIssueFields.push({
                            id: parentIssueFields[i].id,
                            label: parentIssueFields[i].name
                        });
                    }
                }
                $scope.problemReporter = $window.mailerUrl != undefined;
            });
        }
    }
]);

});


