define('app/controllers/timesheetController',['app/timesheetApp', 'moment'], function(app, moment) {
app.controller('TimesheetController', ['$window', '$scope', '$rootScope', 'localize', '$timeout', '$q', '$route', '$routeParams',
   '$location', '$sce', 'pivottableService', 'configurationService', 'loggedInUser', 'projectKey', 'applicationLoggingService',
    function ($window, $scope, $rootScope, localize, $timeout, $q, $route, $routeParams,
        $location, $sce, pivottableService, configurationService, loggedInUser, projectKey, logger) {

        var COOKIE_NAME_SUFFIX = $scope.dashboardId ? '-' + $scope.dashboardId + '-' + $scope.dashboardItemId : '';
        var COOKIE_NAME_LOCATION_SEARCH = 'timereports-location-search' + COOKIE_NAME_SUFFIX;

        var saveRouteParams = function (routeParams) {
            AP.require('cookie', function(cookie) {
                if (angular.isFunction(cookie.save)) {
                    delete routeParams._;
                    cookie.save(COOKIE_NAME_LOCATION_SEARCH, JSON.stringify(routeParams));
                    routeParams._ = Date.now();
                }
            });
        };

        var $cookieDeferred = $q.defer();
        AP.require('cookie', function (cookie) {
            if (angular.isFunction(cookie.read)) {
                cookie.read(COOKIE_NAME_LOCATION_SEARCH, function (data) {
                    var savedParams = TimesheetUtils.getJson(data || '');
                    angular.extend(savedParams, $routeParams);
                    $cookieDeferred.resolve(savedParams);
                });
            } else {
                $cookieDeferred.resolve($routeParams);
            }
        });
        $cookieDeferred.promise.then(function(routeParams) {
            if ($scope.isGadget) {
                pivottableService.getGadgetConfiguration($scope.dashboardId, $scope.dashboardItemId).then(function(config) {
                    return angular.extend(routeParams, config);
                }).then(init);
            } else {
                init(routeParams);
            }
        });

    function init(routeParams) {
        AP.resize();
        AJS.$('.aui-inline-dialog').hide(); // TODO: AP.InlineDialog.hide(), see ACJS-46

        //utils
        $scope.TimesheetUtils = TimesheetUtils;
        $scope.showGadgetConfiguration = false;

        $scope.pivotTableType = routeParams.pivotTableType || 'Timesheet';
        if ($scope.pivotTableType == 'PlannedVsActualTime') { // FIXME: remove
            $scope.pivotTableType = 'TimeTracking';
        }
        $scope.contentTemplate = function() {
            if ($scope.pivotTableType == 'Timesheet') {
                return $scope.templates['timesheet']; // furl
            } else if ($scope.pivotTableType == 'PieChart') {
                return $scope.templates['piechart']; // furl
            } else if ($scope.pivotTableType == 'TimeEntry') {
                return '/templates/timeentry.html'; // TODO furl
            } else {
                return $scope.templates['pivottable']; // furl
            }
        };

        var dialogCloseFunction = function(action){
            if (action == 'submit') {
                $scope.refresh();
            }
        };

        if ($scope.isGadget) {
            AP.require('jira', function (jira) {
                var title = localize.getLocalizedString($scope.pivotTableType);
                jira.setDashboardItemTitle(title);
                jira.DashboardItem.onDashboardItemEdit(function () {
                    $scope.showGadgetConfiguration = true;
                    $scope.$apply();
                });
            });
        }

        var openMoreOptionDialog = function() {
            var now = new Date();
            var worklogDate = this.worklog.date;
            worklogDate.setHours(now.getHours());
            worklogDate.setMinutes(now.getMinutes());
            TimesheetUtils.setDynamicParams({
                issueKey: this.worklog.issueKey,
                date: worklogDate
            });
            AP.require('dialog', function (dialog) {
                dialog.create({
                    key: 'createworklog',
                    width: '700px',
                    height: '400px',
                    chrome: true,
                    header: "Log Work",
                    submitText: "Log"
                }).on("close", dialogCloseFunction);
                dialog.getButton('submit').disable();
            });
        };

        var addWorklogFunc = function() {
            var popupData = this;
            var worklog = popupData.worklog;
            popupData.loading = true;
            var startTime = TimesheetUtils.getCurrentTimeMs() - TimesheetUtils.getTimeSpentMs(worklog.timeSpent, $scope.jiraConfig);
            worklog.date = new Date(worklog.date.getTime() + Math.max(0, startTime));
            pivottableService.createWorklog(worklog).then(function(createdWorklog) {
                var entry = new WorkedTimePivotEntry(popupData.rowKey, popupData.columnKey, createdWorklog);
                $scope.pivotTable.addEntry(entry);
            }, function() {
                popupData.loading = false;
            });
        };

        $rootScope.$on('gadgetConfigSave', function() {
            $scope.showGadgetConfiguration = false;
            $scope.refresh();
        });

        $rootScope.$on('gadgetConfigCancel', function() {
            $scope.showGadgetConfiguration = false;
        });

        $scope.addWorklogPopup = {
            worklog: {
                estimateType: 'auto' // for createWorklog.getEstimateParams
            },
            init: function(columnKey, rowKey) {
                this.columnKey = columnKey;
                this.rowKey = rowKey;
                var milliseconds = columnKey.keyValue, issueKey = rowKey.issue.key;
                this.worklog.timeSpent = $scope.jiraConfig.workingHoursPerDay + 'h';
                this.worklog.comment = '';
                this.worklog.date = new Date(milliseconds);
                this.worklog.issueKey = issueKey;
            },
            addWorklog: addWorklogFunc,
            openMoreOptionDialog: openMoreOptionDialog
        };

        $scope.editWorklogPopup = {
            worklog: {
                estimateType: 'auto' // for createWorklog.getEstimateParams
            },
            worklogs: [],
            init: function(columnKey, rowKey, rowData) {
                this.columnKey = columnKey;
                this.rowKey = rowKey;
                var milliseconds = columnKey.keyValue, issueKey = rowKey.issue.key;
                this.worklogs = [];
                this.worklog.timeSpent = $scope.jiraConfig.workingHoursPerDay + 'h';
                this.worklog.comment = '';
                this.worklog.date = new Date(milliseconds);
                this.worklog.issueKey = issueKey;
                for (var i = 0; i < rowData.length; i++) {
                    if (TimesheetUtils.sameDay(this.worklog.date, new Date(rowData[i].columnKey.keyValue))) {
                        this.worklogs.push(rowData[i].worklog);
                    }
                }
            },
            openDeleteWorklogDialog: function(worklogId, parentIssue) {
                TimesheetUtils.setDynamicParams({
                    issueKey: parentIssue ? parentIssue.key : this.worklog.issueKey,
                    worklogId: worklogId
                });
                AP.require('dialog', function (dialog) {
                    dialog.create({
                        key: 'deleteworklog',
                        width: '700px',
                        height: '250px',
                        chrome: true,
                        header: "Delete Worklog",
                        submitText: "Delete"
                    }).on("close", dialogCloseFunction);
                    dialog.getButton('submit').disable();
                });
            },
            openEditWorklogDialog: function(worklogId, parentIssue) {
                TimesheetUtils.setDynamicParams({
                    issueKey: parentIssue ? parentIssue.key : this.worklog.issueKey,
                    worklogId: worklogId
                });
                AP.require('dialog', function (dialog) {
                    dialog.create({
                        key: 'updateworklog',
                        width: '700px',
                        height: '400px',
                        chrome: true,
                        header: "Log Work",
                        submitText: "Log"
                    }).on("close", dialogCloseFunction);
                    dialog.getButton('submit').disable();
                });
            },
            openMoreOptionDialog: openMoreOptionDialog,
            addWorklog: addWorklogFunc
        };

        $scope.addWorklogDialog = function() {
            TimesheetUtils.setDynamicParams({
                date: new Date()
            });
            AP.require('dialog', function (dialog) {
                dialog.create({
                    key: 'addworklog',
                    width: '700px',
                    height: '400px',
                    chrome: true,
                    header: "Log Work",
                    submitText: "Log"
                }).on("close", dialogCloseFunction);
            });
        };

        // STATIC and COOKIE DATA
        // FIXME: cookies are not allowed in iframe:
        // https://ecosystem.atlassian.net/browse/AC-900
        // rework to use backing db/plugin settings/user preferences
        $scope.isMonthView = routeParams.view === 'month';
        $scope.sum = routeParams.sum || ($scope.isMonthView ? 'week' : 'day');
        $scope.compressed = (routeParams.showDetails == false || routeParams.showDetails == 'false');
        $scope.startDate = routeParams.startDate ? moment(routeParams.startDate).toDate() : undefined;
        $scope.endDate = routeParams.endDate ? moment(routeParams.endDate).toDate() : undefined;
        $scope.numOfWeeks = routeParams.numOfWeeks || 1;
        if ($scope.pivotTableType != 'TimeTracking') { // gadgets and reports
            var numOfWeeks = $scope.numOfWeeks;
            var monthView = routeParams.view == 'month';
            var calculatedStartDate = TimesheetUtils.getLastMondayDate();
            calculatedStartDate = TimesheetUtils.addWeeks(calculatedStartDate, 1 - numOfWeeks);
            var firstDayOfTheMonth = TimesheetUtils.getFirstDayOfTheMonth();
            if (routeParams.startDate === undefined) {
                $scope.startDate = monthView
                            ? firstDayOfTheMonth
                            : calculatedStartDate;
            }
            if (routeParams.endDate === undefined) {
                var fromDate = $scope.startDate || calculatedStartDate;
                $scope.endDate = monthView
                            ? TimesheetUtils.getLastDayOfTheMonth(fromDate)
                            : TimesheetUtils.addDays(fromDate, (7 * (numOfWeeks - 1)) + 6);
            }
        }
        $scope.projectKey = projectKey;
        $scope.sumSubTasks = routeParams.sumSubTasks == true || routeParams.sumSubTasks == 'true';
        $scope.filterOrProjectId = routeParams.filterOrProjectId || (projectKey ? 'project_' + projectKey : undefined);
        $scope.numOfWeeks = routeParams.numOfWeeks || 1;

        if ($scope.pivotTableType == 'TimeTracking') {
            $scope.hideTotalColumn = true;
            $scope.hideDetails = routeParams.groupByField == undefined || routeParams.groupByField == 'workeduser';
            if ($scope.hideDetails) {
                $scope.compressed = true;
            }
            $scope.hidePrevNext = true;
        } else {
            $scope.group = routeParams.group;
            if ($scope.pivotTableType == 'IssueWorkedTimeByUser' || $scope.pivotTableType == 'IssueWorkedTimeByStatus' || $scope.pivotTableType == 'PieChart') {
                $scope.hidePrevNext = routeParams.startDate === null && routeParams.endDate === null;
            }
            if ($scope.pivotTableType == 'Timesheet' || $scope.pivotTableType == 'PieChart') {
                $scope.user = decodeURIComponent(routeParams.user || '');
                if ($scope.pivotTableType =='Timesheet'
                        && !routeParams.group && (!$scope.user || $scope.user == loggedInUser.key)
                        && !routeParams.groupByField) {
                    pivottableService.havePermission('WORK_ON_ISSUES').then(function(value) {
                        $scope.editWorklogPopup.canLogWork = $scope.canLogWork = value;
                    });
                    pivottableService.havePermission('EDIT_OWN_WORKLOGS').then(function(value) {
                        $scope.editWorklogPopup.canEditOwnWorklog = $scope.canEditOwnWorklog = value;
                    });
                    pivottableService.havePermission('DELETE_OWN_WORKLOGS').then(function(value) {
                        $scope.editWorklogPopup.canDeleteOwnWorklog = $scope.canDeleteOwnWorklog = value;
                    });
                    // TODO: EDIT_ALL_WORKLOGS, DELETE_ALL_WORKLOGS
                }
            }
            if ($scope.pivotTableType == 'PieChart') {
                $scope.hideDetails = true;
            }
        }

        $scope.changeWeek = function(value) {
            var newStartDate = TimesheetUtils.changeDate($scope.startDate, value, $scope.pivotTableType != 'Timesheet', $scope.isMonthView);
            if (newStartDate != null) {
                routeParams.startDate = newStartDate;
            }
            var newEndDate = TimesheetUtils.changeDate($scope.endDate, value, true, $scope.isMonthView);
            if (newEndDate != null) {
                routeParams.endDate = newEndDate;
            }
            saveRouteParams(routeParams);
            $location.search(routeParams);
        };

        $scope.showPrevToday = function() {
            return $scope.startDate && isOutOfTodayGreater(TimesheetUtils.addWeeks($scope.startDate, $scope.numOfWeeks - 1), new Date());
        };

        $scope.showNextToday = function() {
            return $scope.startDate && isOutOfTodayGreater(new Date(), TimesheetUtils.addWeeks($scope.startDate, $scope.numOfWeeks - 1));
        };

        var isOutOfTodayGreater = function(date1, date2) {
            return !moment().isBetween(TimesheetUtils.addWeeks($scope.startDate, $scope.numOfWeeks - 1), $scope.endDate) && date1.getTime() > date2.getTime();
        };

        $scope.moveToday = function() {
            delete routeParams.startDate;
            delete routeParams.endDate;
            saveRouteParams(routeParams);
            $location.search(routeParams);
        };

        $scope.toggleMonthView = function() {
            if ($scope.isMonthView) {
                delete routeParams.view;
                delete routeParams.endDate;
            } else {
                var startDate = TimesheetUtils.getFirstDayOfTheMonth($scope.startDate || $scope.endDate);
                var endDate = TimesheetUtils.getLastDayOfTheMonth($scope.startDate || $scope.endDate);
                routeParams.startDate = TimesheetUtils.formatDateForSearch(startDate);
                routeParams.endDate = TimesheetUtils.formatDateForSearch(endDate);
                routeParams.view = 'month';
            }
            saveRouteParams(routeParams);
            $location.search(routeParams);
        };

        $scope.toggleShowDetails = function() {
            if ($scope.compressed) {
                delete routeParams.showDetails;
            } else {
                routeParams.showDetails = false;
            }
            saveRouteParams(routeParams);
            $location.search(routeParams);
        };

        $scope.groupByChanged = function() {
            if (!$scope.groupByOptions.selected) {
                delete routeParams.groupByField;
            } else {
                routeParams.groupByField = $scope.groupByOptions.selected;
            }
            saveRouteParams(routeParams);
            $location.search(routeParams);
        };

        $scope.moreFieldsChanged = function() {
            if (!$scope.moreFieldsOptions.selected) {
                delete routeParams.moreFields;
            } else {
                routeParams.moreFields = $scope.moreFieldsOptions.selected;
            }
            saveRouteParams(routeParams);
            $location.search(routeParams);
        };

        $scope.filterByChanged = function(values) {
            if (values != null && values != "") {
                routeParams.filterOrProjectId = values.split(',');
            } else {
                delete routeParams.filterOrProjectId;
            }
            saveRouteParams(routeParams);
            $location.search(routeParams);
            $scope.$apply();
        };

        $scope.pivotTableTypeChanged = function() {
            if (!$scope.pivotTableTypeOptions.selected) {
                delete routeParams.pivotTableType;
            } else {
                routeParams.pivotTableType = $scope.pivotTableTypeOptions.selected;
            }
            saveRouteParams(routeParams);
            $location.search(routeParams);
        };

        $scope.groupChange = function(groups) {
            if (groups != null && groups != "") {
                routeParams.group = groups.split(',');
            } else {
                delete routeParams.group;
            }
            saveRouteParams(routeParams);
            $location.search(routeParams);
            $scope.$apply();
        };

        $scope.startChangeDate = function(date) {
            changeDate('startDate', date);
        };

        $scope.endChangeDate = function(date) {
            changeDate('endDate', date);
        };

        var changeDate = function(prop, date) {
            if (date != null && date != '') {
                routeParams[prop] = date;
            } else {
                delete routeParams[prop];
            }
            saveRouteParams(routeParams);
            $location.search(routeParams);
            $scope.$apply();
        };

        $scope.userChange = function(user) {
            routeParams.user = encodeURIComponent(user || '');
            saveRouteParams(routeParams);
            $location.search(routeParams);
            $scope.$apply();
        };

        $scope.sumSubTasksChange = function(sumSubTasks) {
            if (sumSubTasks) {
                routeParams.sumSubTasks = true;
            } else {
                delete routeParams.sumSubTasks;
            }
            saveRouteParams(routeParams);
            $location.search(routeParams);
        };

        $scope.refresh = function() {
            pivottableService.resetState();
            configurationService.resetState();
            $route.reload();
        };

        $scope.removeMenuOption = function(key, value) {
            var item = routeParams[key];
            if (angular.isArray(item) && item.length > 1) {
                item.splice(item.indexOf(value), 1);
            } else if (false && item === undefined) { // disabled
                routeParams[key] = null;
            } else {
                delete routeParams[key];
            }
            saveRouteParams(routeParams);
            $location.search(routeParams);
        };

        $scope.getSelectedOptionLabels = function() {
            var result = [];
            var keys = Object.keys(routeParams);
            var addToResult = function(key, value) {
                var i18nParam = 'param.' + key;
                if ('filterOrProjectId' == key) {
                    var option = $scope.filterByOptions.findOption(value);
                    if (option != null) {
                        value = option.label;
                        if (option.id.indexOf('project_') == 0) {
                            i18nParam += '.project';
                        }
                        if (option.id.indexOf('filter_') == 0) {
                            i18nParam += '.filter';
                        }
                    }
                } else if ('groupByField' == key) {
                    var option = $scope.groupByOptions.findOption(value);
                    if (option != null) {
                        value = option.label;
                    }
                } else if ('moreFields' == key) {
                    var option = $scope.moreFieldsOptions.findOption(value);
                    if (option != null) {
                        value = option.label;
                    }
                } else if ('user' == key) {
                    if ($scope.paramUserInfo != null) {
                        value = $scope.paramUserInfo.displayName;
                    } else {
                        value = loggedInUser.fullName;
                    }
                } else if ('startDate' == key || 'endDate' == key) {
                    value = moment(value).format('YYYY-MM-DD'); // TODO: format('l') along with date picker
                }
                result.push({
                    label: i18nParam,
                    labelParam: value,
                    key: key
                });
            };
            for (var i = 0; i < keys.length; i++) {
                var key = keys[i];
                if (['user', 'startDate', 'endDate', 'groupByField', 'moreFields', 'filterOrProjectId', 'group'].indexOf(key) == -1) {
                    continue;
                } else if ('user' == key && ['Timesheet', 'PieChart'].indexOf($scope.pivotTableType) == -1) {
                    continue;
                } else if ($scope.pivotTableType == 'PieChart' && 'moreFields' == key) {
                    continue;
                } else if ($scope.pivotTableType == 'TimeTracking' && ['group', 'startDate', 'endDate'].indexOf(key) != -1) {
                    continue;
                } else if ($scope.pivotTableType == 'TimeTracking' && 'groupByField' == key && routeParams[key] == 'workeduser') {
                    continue;
                } else if (routeParams['group'] != null && key == 'user') {
                    continue;
                }
                var value = routeParams[key];
                if (angular.isArray(value)) {
                    for (var j = 0; j < value.length; j++) {
                        addToResult(key, value[j]);
                    }
                } else if (value !== null) {
                    addToResult(key, value);
                }
            }
            if (!routeParams.user && !routeParams.group && $scope.pivotTableType == 'Timesheet') {
                addToResult('user', $scope.user || loggedInUser.key);
            }
            if (!routeParams.filterOrProjectId && $scope.projectKey) {
                addToResult('filterOrProjectId', 'project_' + $scope.projectKey);
            }
            if ($scope.pivotTableType != 'TimeTracking') {
                if (routeParams.startDate === undefined) {
                    addToResult('startDate', $scope.startDate);
                } else if (routeParams.startDate === null) {
                    addToResult('startDate', moment('1970-01-01'));
                }
                if (routeParams.endDate === undefined) {
                    addToResult('endDate', $scope.endDate);
                } else if (routeParams.endDate === null) {
                    addToResult('endDate', moment('2099-12-31'));
                }
            }
            return result;
        };

        var getLicenseStatus = function() {
            var msg = "";
            if ($scope.license == 'expired') {
                msg = '<span class="red">LICENSE EXPIRED</span>';
            } else if ($scope.license == 'none') {
                msg = '<span class="red">NO LICENSE</span>';
            } else if ($scope.license != 'active') {
                msg = '<span class="red">LICENSE ERROR</span>';
            }

            return {
                isActive: $scope.license == 'active',
                message: $sce.trustAsHtml(msg)
            };
        };
        $scope.licenseStatus = getLicenseStatus();

        $scope.excelView = function() {
            var moreFields = [];
            if (routeParams.moreFields) {
                for (var i = 0; i < routeParams.moreFields.length; i++) {
                    var moreField = routeParams.moreFields[i];
                    var option = $scope.groupByOptions.findOption(moreField);
                    moreFields.push(option);
                }
            }
            if (routeParams.groupByField) {
                var option = $scope.groupByOptions.findOption(routeParams.groupByField);
                moreFields.push(option);
            }
            var options = {
                pivotTableType: $scope.pivotTableType,
                moreFields: moreFields,
                monthView: $scope.isMonthView,
                startDate: $scope.startDate,
                endDate: $scope.endDate,
                user: $scope.user || loggedInUser.key,
                worklogAuthors: $scope.group && $scope.group.length > 0 ? pivottableService.worklogAuthors : null,
                sumSubTasks: $scope.sumSubTasks
            };
            TimesheetUtils.sortByProperty(pivottableService.allIssues, 'key');
            var excelView = new ExcelView(pivottableService.allIssues);
            excelView.download(options);
        };

        pivottableService.isAdministrator().then(function(isAdmin){
            $scope.isAdmin = isAdmin;
        });

        // DYNAMIC DATA (uses request params)
        // e.g. http://localhost:5000/#/?groupByField=status
        // http://localhost:5000/#/?view=month&groupByField=assignee
        var options = {
            pivotTableType: $scope.pivotTableType,
            groupByField: routeParams.groupByField,
            moreFields: routeParams.moreFields,
            monthView: $scope.isMonthView,
            startDate: $scope.startDate,
            endDate: $scope.endDate,
            filterOrProjectId: $scope.filterOrProjectId,
            projectKey: $scope.projectKey,
            user: $scope.user || loggedInUser.key,
            groups: !$scope.group || angular.isArray($scope.group) ?
                $scope.group : /* from $routeParams */ [$scope.group],
            sumSubTasks: $scope.sumSubTasks,
            sum: $scope.sum
        };

        $scope.loading = true;

        var $paramUserInfo = pivottableService.getUserInfo($scope.user).then(function(user) {
            $scope.paramUserInfo = user;
            return user;
        });

        pivottableService.restrictedByAuditorsGroups(loggedInUser).then(function(restricted) {
            var restrictable = ($scope.pivotTableType == 'Timesheet' || $scope.pivotTableType == 'PieChart') && (options.groups || ($scope.user && $scope.user != loggedInUser.key));
            restrictable = restrictable || $scope.pivotTableType == 'IssueWorkedTimeByUser';
            if (restrictable && restricted) {
                $scope.loading = false;
                $scope.restrictionError = {'msgKey': 'error.restriction.auditorsGroups'};
            } else if ($window.requireLicense && restrictable && !$scope.licenseStatus.isActive) {
                $scope.loading = false;
                $scope.restrictionError = {'msgKey': 'error.restriction.licenseError'};
            } else {
                $q.all([$paramUserInfo, configurationService.getConfiguration()]) // ACJIRA-300 // FIXME: move to pivottableService.getPivotTable, without getConfiguration
                    .then(function(value) {
                        var user = value[0], config = value[1];
                        options.username = user ? user.name : loggedInUser.id; // ACJIRA-300
                        options.configOptions = {};
                        if (config.compositionIssueLink.val != null) {
                            options.configOptions.compositionIssueLink = config.compositionIssueLink.val;
                        }
                        if (config.parentIssueField.val != null) {
                            options.configOptions.parentIssueField = config.parentIssueField.val;
                        }
                        if (options.pivotTableType == 'TimeTracking' && options.groupByField == 'workeduser') {
                            options.groupByField = '';
                        }
                        if (options.groups != null && options.groups != '') {
                            options.username = '';
                            options.user = '';
                        }
                        $scope.configOptions = options.configOptions;
                    })
                    .then(function(){
                        pivottableService.getPivotTable(loggedInUser, options).then(function(pivotTable) {
                            $scope.loading = false;
                            $scope.pivotTable = pivotTable;
                            $scope.rowKeySize = TimesheetUtils.colspan(pivotTable);
                            AP.resize();
                        })
                    });
            }
        });

        var WORKED_USER_FIELD = {
            id: "workeduser",
            name: "Worked User",
            custom: true,
            schema: {},
            clauseNames: []
        };

        var PARENT_ISSUE_FIELD = {
            id: "parent",
            name: "Parent Issue",
            custom: true,
            schema: {},
            clauseNames: []
        };
        var $fields = pivottableService.getFields().then(function(fields) {
            $scope.groupByOptions = new TimesheetSelectOptions(TimesheetGeneralOption, routeParams.groupByField);
            $scope.moreFieldsOptions = new TimesheetSelectOptions(TimesheetGeneralOption, routeParams.moreFields);
            var filteredFields = TimesheetUtils.filterFields(fields);
            for (var i = 0; i < filteredFields.length; i++) {
                var filteredField = filteredFields[i];
                $scope.groupByOptions.addOption(filteredField.name, filteredField.id);
                $scope.moreFieldsOptions.addOption(filteredField.name, filteredField.id);
            }
            if ($scope.pivotTableType != 'TimeTracking') {
                $scope.groupByOptions.addOption(WORKED_USER_FIELD.name, WORKED_USER_FIELD.id);
            }
            $scope.groupByOptions.addOption(PARENT_ISSUE_FIELD.name, PARENT_ISSUE_FIELD.id);
            $scope.groupByOptions.sort();
            $scope.moreFieldsOptions.sort();
            $scope.groupByOptions.addOption("No grouping", "", true);
        });

        var $filtersAndProjects = $q.all([pivottableService.getFilters(), pivottableService.getProjects()]).then(function (value) {
            var filters = value[0], projects = value[1];
            $scope.filterByOptions = new TimesheetSelectOptions(TimesheetGeneralOption, routeParams.filterOrProjectId);
            for (var i = 0; i < filters.length; i++) {
                $scope.filterByOptions.addOption(filters[i].name, 'filter_' + filters[i].id);
            }
            for (var i = 0; i < projects.length; i++) {
                $scope.filterByOptions.addOption(projects[i].name, 'project_' + projects[i].key);
            }
        });

        $q.all([$fields, $filtersAndProjects, $paramUserInfo]).then(function() {
            $scope.gadgetSummary = new GadgetSummary($scope.getSelectedOptionLabels(), $scope.paramUserInfo || loggedInUser).getSummaryItems();
            $scope.selectedOptionLabels = $scope.getSelectedOptionLabels();
        });

        $scope.pivotTableTypeOptions = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.pivotTableType);
        var pivotTableTypeKeys = Object.keys(PivotTableType);
        for (var i = 0; i < pivotTableTypeKeys.length; i++) {
            $scope.pivotTableTypeOptions.addOption(localize.getLocalizedString(pivotTableTypeKeys[i]), pivotTableTypeKeys[i]);
        }

        $scope.jiraConfig = {
            workingHoursPerDay: 8,
            workingDaysPerWeek: 5,
            defaultUnit: 'minute'
        };
        $scope.$watch('isAdmin', function(isAdmin) {
            if (isAdmin) { // ACJIRA-534
                pivottableService.getJiraConfiguration().then(function(config) {
                    if (!config.timeTrackingConfiguration) {
                        AP.require('messages', function(messages) {
                            messages.error('Time Tracking is currently OFF', "Please enable Time Tracking in Administration - Issues - Time Tracking");
                        });
                        return;   
                    }
                    if (config.timeTrackingConfiguration.workingHoursPerDay) {
                        $scope.jiraConfig.workingHoursPerDay = config.timeTrackingConfiguration.workingHoursPerDay;
                    }
                    if (config.timeTrackingConfiguration.workingDaysPerWeek) {
                        $scope.jiraConfig.workingDaysPerWeek = config.timeTrackingConfiguration.workingDaysPerWeek;
                    }
                    if (config.timeTrackingConfiguration.defaultUnit) {
                        $scope.jiraConfig.defaultUnit = config.timeTrackingConfiguration.defaultUnit;
                    }
                });
            }
        });

        var setSumWeek = function() {
            var setSumWeekValues = function (styleClass, title) {
                $scope.sumTitle = title;
                $scope.sumClass = styleClass;
            };
            if ($scope.sum == 'month') {
                setSumWeekValues('day', 'Show daily worked hours');
            } else if ($scope.sum == 'week') {
                setSumWeekValues('month', 'Sum worked hours by month');
            } else {
                setSumWeekValues('week', 'Sum worked hours by week');
            }
        };

        $scope.weekSumChange = function () {
            routeParams.sum = $scope.sum == 'week' ? 'month' : $scope.sum == 'month' ? 'day' : 'week';
            saveRouteParams(routeParams);
            $location.search(routeParams);
        };

        setSumWeek();
    }
}]);

});

